.formCreateTodo{
  border: 2px solid #202329;
  border-radius: 8px;
  width: 90%;
  max-width: 300px;
  background-color: #fff;
  padding: 33px 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  
}

.formCreateTodo label{
  text-align: center;
  font-weight: bold;
  font-size: 18px;
  color: #1E1E1F;
  margin: 5px 0px;
}

.formCreateTodo textarea{
  background-color: #F9FBFC;
  border: 2px solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32,35,41,0.25);
  color: #1E1E1F;
  font-size: 14px;
  text-align: center;
  padding: 12px;
  height: 96px;
  width:calc(100% - 25px);
}

.formCreateTodo textarea::placeholder, input[type=text]::placeholder{
  color: #A5A5A5;
  font-family: 'Montserrat';
  font-weight: 400;
}

.formCreateTodo textarea:focus, input[type=text]:focus{
  outline-color: #3f6e5f;
}


.formCreateTodo input[type=text]{
  background-color: #F9FBFC;
  border: 2px solid #202329;
  border-radius: 2px;
  box-shadow: 0px 5px 50px rgba(32,35,41,0.25);
  color: #1E1E1F;
  font-size: 14px;
  text-align: center;
  padding: 12px;
  height: auto;
  width:calc(100% - 25px);
}


.TodoForm-buttonContainer{
  margin-top: 14px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}


.TodoForm-button{
  cursor: pointer;
  display: inline-block;
  font-size: 15px;
  color:white;
  font-weight: 400;
  padding: 10px 15px;
  border-radius: 5px;
  border: none;
  font-family: 'Montserrat';
}

.TodoForm-button--add{
  background: #295c59;
  box-shadow: 0px 5px 25px rgba(97,218, 250, 0.5);
}

.TodoForm-button--cancel{
  background: #295c59;
  box-shadow: 0px 5px 25px rgba(97,218, 250, 0.5);
}
